.video_responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  width: 70%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.video_responsive iframe {
  left: 0;
  top: 0;
  height: 75%;
  width: 100%;
  position: absolute;
}

@media (max-width: 900px) {
  .video_responsive {
    overflow: hidden;
    padding-bottom: 56.25%;
    position: relative;
    width: 70%;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  .video_responsive iframe {
    left: 0;
    top: 0;
    height: 75%;
    width: 100%;
    position: absolute;
  }
}

@media (max-width: 800px) {
  .video_responsive {
    overflow: hidden;
    padding-bottom: 56.25%;
    position: relative;
    width: 80%;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  .video_responsive iframe {
    left: 0;
    top: 0;
    height: 75%;
    width: 100%;
    position: absolute;
  }
}
